import { FormControl, FormGroup, Validators } from '@angular/forms';
import { UserService } from './../../services/user.service';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-altura',
  templateUrl: './altura.page.html',
  styleUrls: ['./altura.page.scss'],
})
export class AlturaPage implements OnInit {
  form!:FormGroup;
  peso;
  constructor(
    private router: Router,
    private userSrv: UserService
  ) { }

  ngOnInit() {
    this.getWeight()
    this.initialForm()
  }

  getWeight(){
    this.userSrv.getObservableWeight().subscribe(data=>{
      this.peso = data;
      console.log(this.peso)
    })
  }

  initialForm(){
    this.form = new FormGroup({
      altura: new FormControl(null,[Validators.required]),
      peso: new FormControl(null)
    })
  }

  submitHeight(){
    this.form.value.peso = this.peso
    let height = this.form.value
    
    this.userSrv.insertHeight(height).subscribe(()=>{
       this.submitImc();
      this.router.navigate(['resultado'])
    })
    
  }

  submitImc(){
    this.userSrv.insertImc(this.form.value).subscribe(()=>{

    })
  }

  onBack(){
    this.router.navigate(['anamnese'])
  }

}
